
body{
  background-color: aquamarine;
  height: 100vh;
  overflow: hidden;
}

.main {
margin: 15% 5%;
  text-align: center;
  background-color: rgb(232, 232, 200);
  padding: 20px;
border-radius: 30px;
color: darkblue;
}

.main-counter{
margin: 10px;
}

.button{
  border-radius: 25px;
  padding: 25px 40px;
  background-color: darkblue;
  font-size: xx-large;
  color: white;
  margin: 10px;
}
.clear{
  border-radius: 40px;
  padding: 5px 10px;
  background-color: rgb(217, 40, 24);
  font-size: small;
  color: white;
}


input{
  color: darkblue;
  border-radius: 20px;
  padding: 0 5px;
  background: aquamarine;
}